export const GET_GROUPS_LISTING = "GET_GROUPS_LISTING";
export const CREATE_GROUP = "CREATE_GROUP";
export const DELETE_GROUP = "DELETE_GROUP";
export const FETCH_PERMISSIONS = "FETCH_PERMISSIONS";
export const GET_GROUP_BY_ID = "GET_GROUP_BY_ID";
export const UPDATE_GROUP = "UPDATE_GROUP";

export const getGroupsListing = (payload, success = () => {}, fail = () => {}) => {
    return {
        type: GET_GROUPS_LISTING,
        payload,
        success,
        fail
    }
};

export const createGroup = (payload, success = () => {}, fail = () => {}) => {
    return {
        type: CREATE_GROUP,
        payload,
        success,
        fail
    }
};

export const deleteGroup = (payload, success = () => {}, fail = () => {}) => {
    return {
        type: DELETE_GROUP,
        payload,
        success,
        fail
    }
};

export const fetchPermissions = (success = () => {}, fail = () => {}) => {
    return {
        type: FETCH_PERMISSIONS,
        success,
        fail
    }
};

export const getGroupDetailsById = (payload, success = () => {}, fail = () => {}) => {
    return {
        type: GET_GROUP_BY_ID,
        payload,
        success,
        fail
    }
};

export const updateGroup = (payload, success = () => {}, fail = () => {}) => {
    return {
        type: UPDATE_GROUP,
        payload,
        success,
        fail
    }
};