import { all, put, takeLatest } from "redux-saga/effects";
import { clearProfileData, setToken, startLoading, stopLoading } from "redux/Actions/Auth";
import { CREATE_GROUP, DELETE_GROUP, FETCH_PERMISSIONS, GET_GROUPS_LISTING, GET_GROUP_BY_ID, UPDATE_GROUP } from "redux/Actions/PharmacyAdminDashboard";
import { CREATE_OR_UPDATE_BRANCH, CREATE_OR_UPDATE_PHARMACY_TEMPLATE, DELETE_BRANCHES, DELETE_PHARMACY_DOCUMENTS, GET_BRANCH, GET_BRANCH_LISTING, GET_OWNER_INFO, GET_PHARMACY_CS_IN_USE, GET_PHARMACY_INFO, GET_SERVICES, GET_TEMPLATE, POST_BRANCH_STATUS, POST_EDIT_PROFILE_DATA, POST_PHARMACY_INFO, setTemplateData, UPLOAD_PHARMACY_DOCUMENTS } from "redux/Actions/PharmacyAdminProfile";
import { API_URLS, STATUS_CODES } from "services/Api/Constants";
import { deleteRequest, getRequest, postRequest, putRequest, updateAuthToken } from "shared/Axios";
import { objectToFormData } from "shared/utils";

function* getOwnerInfo({ payload, success = () => {}, fail = () => {} }) {
    try {
        yield put(startLoading());

        const { status, data = {} } = yield getRequest({
            API: API_URLS.OWNER_INFO,
        });
        console.log(data, "data at api call");
        success(data);
    } catch (error) {
        if (error?.status === STATUS_CODES.UNAUTHORIZED) {
            updateAuthToken(null);
            yield put(clearProfileData());
            yield put(setToken(""));
        }
        fail(error?.data?.message || error?.data?.detail || error?.message);
    } finally {
        yield put(stopLoading());
    }
}

