function* pharmacyAdminSaga() {
    yield all([
        takeLatest(GET_OWNER_INFO, getOwnerInfo),
        takeLatest(GET_PHARMACY_INFO, getPharmacyInfo),
        takeLatest(POST_EDIT_PROFILE_DATA, postEditProfileData),        
        takeLatest(POST_PHARMACY_INFO, postPharmacyInfo),        
        takeLatest(GET_PHARMACY_CS_IN_USE, getCSInUse),        
        takeLatest(GET_BRANCH, getBranch),        
        takeLatest(CREATE_OR_UPDATE_BRANCH, postBranchInfo),        
        takeLatest(GET_SERVICES, getServices),
        takeLatest(CREATE_OR_UPDATE_PHARMACY_TEMPLATE, createTemplate),   
        takeLatest(GET_TEMPLATE, getTemplate),   
        takeLatest(GET_BRANCH_LISTING, fetchBranches),   
        takeLatest(POST_BRANCH_STATUS, changeBranchStatus),   
        takeLatest(DELETE_BRANCHES, deleteBranch),
        takeLatest(UPLOAD_PHARMACY_DOCUMENTS, uploadPharmacyDocument),
        takeLatest(DELETE_PHARMACY_DOCUMENTS, deletePharmacyDocument),
        takeLatest(GET_GROUPS_LISTING, fetchGroups),   
        takeLatest(CREATE_GROUP, createGroup),   
        takeLatest(DELETE_GROUP, deleteGroup),   
        takeLatest(FETCH_PERMISSIONS, fetchPermissions),
        takeLatest(GET_GROUP_BY_ID, getGroupDetailsById),
        takeLatest(UPDATE_GROUP, updateGroup),
    ]);
}

export default pharmacyAdminSaga;