import React, { useEffect, useState } from "react";
import "./style.scss";
import { LABEL, initialDaysData } from "shared/Constants";

const days = [
  "Monday",
  "Tuesday",
  "Wednesday",
  "Thursday",
  "Friday",
  "Saturday",
  "Sunday"
];

let labels = {
  opening_time: "Opening Time",
  closing_time: "Closing Time",
  lunch_start: "Lunch Start",
  lunch_end: "Lunch End"
};

const JobTiming = ({ data = [], setJobTimingData, handleSetIsSelectedAll, isSelectAll }) => {

  const [selectedDay, setSelectedDay] = useState(0);
  const [selectAll, setSelectAll] = useState(isSelectAll ? true : false);
  const [timings, setTimings] = useState({
    opening_time: "",
    closing_time: "",
    lunch_start: "",
    lunch_end: ""
  });

  const [dayData, setDayData] = useState(data.length ? [...data] : initialDaysData);
  const [selectAllDaysData, setSelectAllDaysData] = useState(data.length ? [...data] : initialDaysData);

  useEffect(() => {
    if (selectAll) {
      updateSelectAllTimingData(
        selectedDay,
        timings.closing_time,
        timings.opening_time,
        timings.lunch_start,
        timings.lunch_end
      );
    } else {
      updateJobTimingData(
        selectedDay,
        timings.closing_time,
        timings.opening_time,
        timings.lunch_start,
        timings.lunch_end
      );
    }
  }, [timings]);

  useEffect(() => {
    let filteredDay = {};
    if (selectAll) {
      filteredDay =
        selectAllDaysData?.find(
          (dayDetails) => dayDetails?.day === selectedDay
        ) || {};
    } else {
      filteredDay =
        dayData?.find((dayDetails) => dayDetails?.day === selectedDay) || {};
    }
    setTimings({
      opening_time: filteredDay.opening_time || "",
      closing_time: filteredDay.closing_time || "",
      lunch_start: filteredDay.lunch_start || "",
      lunch_end: filteredDay.lunch_end || ""
    });
  }, [selectedDay]);

  useEffect(() => {
    if (selectAll) {
      days.forEach((item, key) => {
        updateSelectAllTimingData(
          key,
          timings.closing_time,
          timings.opening_time,
          timings.lunch_start,
          timings.lunch_end
        );
      });
    } else {
      let filteredDay =
        dayData?.find((dayDetails) => dayDetails?.day === selectedDay) || {};
      setTimings({
        opening_time: filteredDay.opening_time || "",
        closing_time: filteredDay.closing_time || "",
        lunch_start: filteredDay.lunch_start || "",
        lunch_end: filteredDay.lunch_end || ""
      });
    }
  }, [selectAll]);

  const handleSelectDay = (index) => {
    setSelectedDay(index);
  };

  const handleSelectAll = () => {
    handleSetIsSelectedAll(!selectAll);
    setSelectAll(!selectAll);
  };

  const handleTimeChange = (event, field) => {
    setTimings({ ...timings, [field]: event.target.value });
  };

  function updateJobTimingData(
    day,
    closing_time,
    opening_time,
    lunch_start,
    lunch_end
  ) {
    setDayData((prevJobTimingData) => {
      const updatedData = prevJobTimingData.map((data) => {
        if (data.day === day) {
          return {
            ...data,
            closing_time,
            opening_time,
            lunch_start,
            lunch_end
          };
        }
        return data;
      });
      return updatedData;
    });
  }

  function updateSelectAllTimingData(
    day,
    closing_time,
    opening_time,
    lunch_start,
    lunch_end
  ) {
    setSelectAllDaysData((prevJobTimingData) => {
      const updatedData = prevJobTimingData.map((data) => {
        if (data.day === day) {
          return {
            ...data,
            closing_time,
            opening_time,
            lunch_start,
            lunch_end
          };
        }
        return data;
      });
      return updatedData;
    });
  }

  useEffect(() => {
    if (selectAll) {
      setJobTimingData([...selectAllDaysData])
    } else {
      setJobTimingData([...dayData])
    }
  }, [dayData, selectAllDaysData])

  useEffect(() => {
    if (data.length) {
      setDayData([...data]);
      setSelectAllDaysData([...data]);
      let filteredDay =
        data?.find((dayDetails) => dayDetails?.day === selectedDay) || {};
      setTimings({
        opening_time: filteredDay.opening_time || "",
        closing_time: filteredDay.closing_time || "",
        lunch_start: filteredDay.lunch_start || "",
        lunch_end: filteredDay.lunch_end || ""
      });
    }
  }, [data])

  return (
    <>
      <div className="mb-4">
        <h6 className="label_title mb-4">
          <span>{LABEL.JOB_TIMINGS}</span>
        </h6>
        <div className="d-flex flex-wrap job_btn">
          {days.map((day, index) => (
            <button
              key={index}
              className={`btn btn-outline-primary btn-md me-2 mb-2 ${selectedDay === index ? "selected" : ""}`}
              onClick={() => handleSelectDay(index)}
            >
              {day}
            </button>
          ))}
        </div>
      </div>
      <div className="row">
        {Object.keys(labels).map((field, index) => (
          <div className="col-md-3 mb-4" key={index}>
            <label htmlFor={field}>{`${labels[field]}`}</label>
            <input
              type="time"
              id={field}
              value={timings[field]}
              className="form-control"
              onChange={(event) => handleTimeChange(event, field)}
            />
          </div>
        ))}
      </div>
      <div className="checkbox mb-4">
        <input
          type="checkbox"
          id="selectAll"
          checked={selectAll}
          onChange={handleSelectAll}
        />
        <label htmlFor="selectAll">Select all for same day</label>
      </div>
    </>
  );
};

export default JobTiming;
